import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { Typography } from '@material-ui/core';
import HomePageOne from './HomePageOne';
import { Button } from '@material-ui/core';
import { Link } from 'react-router-dom';
const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
    bpaper:{
    display:'flex',
    padding: theme.spacing(3), 
    justifyContent:'space-around'
    },
  cpaper:{
    padding: theme.spacing(1),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
    },
  fixedHeight: {
    height: 'auto',
  },
}));

export default function Dashboard() {
  const classes = useStyles();
  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
  
  return (<>
        <Grid container spacing={3}>
            <Grid item xs={12} md={12} lg={12}>
                <Paper className={classes.cpaper}>
                    <HomePageOne />
                </Paper>
            </Grid>
        <Grid item xs={12} md={12} lg={12}>
            <Paper className={fixedHeightPaper}>
                <Typography>
                    <b>About This App:</b><br/>FormBook App created with <br></br>
                    react.js , node/express.js ,material ui ,jsonwebtoken.js,bcrypt.js,axios,mongoose<br></br>
                    You are more then welcome to find bugs and contact me<br/>  
                    <Link color="inherit" href='linkedin.com/in/hen-moshe-6a0373215'>
                        Hen Moshe
                    </Link> :)
                </Typography>
            </Paper>
        </Grid>
            <Grid item xs={12}md={12} lg={12}> 
                <Paper className={classes.bpaper}>
                   <Link to='/login'><Button color='success' size='large' variant='contained'>Log-In</Button></Link>
                    <Link to='signup'><Button variant='contained'>Sign-Up</Button></Link>
                </Paper>
            </Grid>
        </Grid>

  </>
  );
}
